// Додає меню
export const addMenuBd = async (group_id, menuObject, groupName) => {
  try {
    // ---------- Додаєм меню -----------------
    const menuGrroupRef = doc(db, 'menu', group_id);
    const path = 'menu.' + menuObject.id;
    await updateDoc(menuGrroupRef, {
      [path]: menuObject,
    });
    // ---------- /Додаєм меню -----------------

    // ---------- Додаєм історію ----------------
    const menuHistoryRef = doc(db, 'menuHistory', menuObject.menu_history_id);
    const menuHistoryInfo = {
      group_id: group_id,
      groupName: groupName,
      id: new Date().getTime(),
      date: new Date().toLocaleDateString(), //дата в формате 27.07.2022 ', new Date().toLocaleDateString()
      time: new Date().toLocaleTimeString(), //час в формате 13:54:43', new Date().toLocaleTimeString()
      day: new Date().getDay(), // возвращает день недели new Date().getDay();
    };
    setDoc(menuHistoryRef, { [menuObject.id]: menuHistoryInfo }, { merge: true });

    // ---------- /Додаєм історію ----------------

    // alert('Меню додано успішно.');
    return true;
  } catch {
    alert('Не вдалося додати меню.');

    return false;
  }
};
// Отримуєм меню з BD
export const getMenuGrupDb = async (colectionName, document) => {
  const docRef = doc(db, colectionName, document);

  try {
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      return docSnap.data().menu;
    } else {
      return [];
    }
  } catch {
    return 'error';
  }
};

// -------------- /Отримує історію меню ------------------------
export const getHistoryMenuBd = async (colection, document) => {
  const docRef = doc(db, colection, document);
  const docSnap = await getDoc(docRef);

  if (docSnap.exists()) {
    return docSnap.data();
  } else {
    return {};
  }
};
// -------------- Отримує історію меню ------------------------
// Видаляє меню
export const removeMenuBd = async (group_id, indexRenderMenuDey, menuDay) => {
  // --------- Видаляє з меню ---------
  const menuRef = doc(db, 'menu', group_id);
  const menuPath = 'menu.' + indexRenderMenuDey;

  await updateDoc(menuRef, {
    [menuPath]: deleteField(),
  });
  // --------- /Видаляє з меню ---------

  // --------- Видаляє з історії ---------
  const menuHistory = await getHistoryMenuBd('menuHistory', menuDay.menu_history_id);

  if (Object.keys(menuHistory).length > 1) {
    const historyMenuRef = doc(db, 'menuHistory', menuDay.menu_history_id);
    const historyPath = menuDay.id;

    await updateDoc(historyMenuRef, {
      [historyPath]: deleteField(),
    });
  } else {
    await deleteDoc(doc(db, 'menuHistory', menuDay.menu_history_id));
  }
  // --------- /Видаляє з історії -------------------
};

