// Додає користувача
const addUser = async (data) => {
  const userData = {
    name: data.name,
    surname: data.surname,
    fullName: data.fullName,
    user_id: data.client_id,
    participationGroups: {},
    accessMeasurements: {},
  };

  try {
    await setDoc(doc(db, 'users', data.client_id), userData);
    return true;
  } catch (e) {
    return false;
  }
};
const addUserMeasurements = async (data) => {
  const userMeasurements = {
    client_id: data.client_id,
    name: data.name,
    surname: data.surname,
    fullName: data.fullName,
    time_milliseconds: data.time_milliseconds,
    date_time: data.date_time,
    date_day: data.date_day,
    date: data.date,
    measurements: [],
  };

  try {
    await setDoc(doc(db, 'measurements', data.client_id), userMeasurements);
    return true;
  } catch (e) {
    alert('Не вдалося додати заміри.');
    return false;
  }
};
const addLoginWebCounter = async (data) => {
  const userMeasurements = {
    client_id: data.client_id,
    name: data.name,
    surname: data.surname,
    fullName: data.fullName,

    time_milliseconds: data.time_milliseconds,
    date_time: data.date_time,
    date_day: data.date_day,
    date: data.date,

    groupMember: [], //до яких груп має доступ

    counterAll: 0, //Рахує всі перегляди програми (буде обнулятись)
    counterAllTime: 0, //Рахує всі перегляди програми
    counterAllTimeGroup: 0, //Рахує всі перегляди програми (буде обнулятись одєльно )

    counterIos: 0,
    counterAndroid: 0,
    counterWindows: 0,
    counterOther: 0,
    timeViewMilliseconds: 0,
    timeViewMillisecondsAllTime: 0,
    timeViewMillisecondsGropTime: 0,
  };

  try {
    await setDoc(doc(db, 'loginWebCounter', data.client_id), userMeasurements);
    return true;
  } catch (e) {
    alert('Не вдалося додати loginWebCounter.');
    return false;
  }
};

// Додає клієнта і користувача
export const addClientBd = async (clientData) => {
  // Додає користувача
  const isAddUser = await addUser(clientData);
  // Додає користувача
  const isAddMeasurements = await addUserMeasurements(clientData);
  const isAddLoginWebCounter = await addLoginWebCounter(clientData);

  // Якщо користувача додали успішно додаєм клієнта
  if (isAddUser && isAddMeasurements && isAddLoginWebCounter) {
    try {
      await setDoc(doc(db, 'clients', clientData.client_id), clientData);
      return true;
    } catch (e) {
      await deleteDoc(doc(db, 'users', clientData.client_id));
      await deleteDoc(doc(db, 'measurements', clientData.client_id));
      await deleteDoc(doc(db, 'loginWebCounter', clientData.client_id));
      alert('Не вдолося додати клієнта.');
      return false;
    }
  } else {
    await deleteDoc(doc(db, 'users', clientData.client_id));
    await deleteDoc(doc(db, 'measurements', clientData.client_id));
    await deleteDoc(doc(db, 'loginWebCounter', clientData.client_id));
    alert('Не вдолося додати клієнта.');
    return false;
  }
};

