export const deleteClientBd = async (client_id) => {
  try {
    await deleteDoc(doc(db, 'clients', client_id));
    await deleteDoc(doc(db, 'users', client_id));
    await deleteDoc(doc(db, 'measurements', client_id));
    await deleteDoc(doc(db, 'loginWebCounter', client_id));
    await deleteDoc(doc(db, 'loginWebList', client_id));
    await deleteDoc(doc(db, 'measurements', client_id));
    return true;
  } catch (e) {
    return false;
  }
};
