export const getGrupListDb = async (
  colectionName = 'groupsList',
  getOrderBy = 'time_milliseconds',
) => {
  const body = query(
    collection(db, colectionName),
    // orderBy(getOrderBy),
    orderBy(getOrderBy, 'desc'),
  );

  const grupListBdArray = await getDocs(body);

  const grupListArray = [];
  grupListBdArray.forEach((doc) => {
    grupListArray.push(doc.data());
  });

  return grupListArray;
};
