export const addClientGroupBd = async (client_id, groupObject, groupObject_link) => {
  const clientParticipationRef = doc(db, 'clients', client_id);
  const usersParticipationRef = doc(db, 'users', client_id);

  const path = 'participationGroups.' + groupObject_link;

  try {
    await updateDoc(clientParticipationRef, {
      [path]: groupObject,
    });
    await updateDoc(usersParticipationRef, {
      [path]: groupObject,
    });
    return true;
  } catch {
    alert('Не вдалося додати клієнта до групи.');
    return false;
  }
};
