export const getDocsOrderByBd = async (colectionName, getOrderBy, byOrder = '') => {
  try {
    const getOrder = (getOrderBy, byOrder) => {
      if (byOrder === 'desc') {
        return orderBy(getOrderBy, 'desc');
      }
      return orderBy(getOrderBy);
    };

    const body = query(
      collection(db, colectionName),
      // orderBy(getOrderBy),
      getOrder(getOrderBy, byOrder),
    );

    const docsBdArray = await getDocs(body);

    const docsArray = [];
    docsBdArray.forEach((doc) => {
      docsArray.push(doc.data());
    });

    return docsArray;
  } catch (e) {
    console.log(e);
    return false;
  }
};
export const getDocsArrayContainsOrderByBd = async (
  colectionName,
  getOrderBy,
  byOrder = '',
  group_id,
) => {
  try {
    const getOrder = (getOrderBy, byOrder) => {
      if (byOrder === 'desc') {
        return orderBy(getOrderBy, 'desc');
      }
      return orderBy(getOrderBy);
    };

    // const body = query(
    //   collection(db, colectionName),
    //   // orderBy(getOrderBy),
    //   getOrder(getOrderBy, byOrder),
    // );

    const body = query(
      collection(db, colectionName),
      where('groupMember', 'array-contains', group_id),
      getOrder(getOrderBy, byOrder),
    );

    const docsBdArray = await getDocs(body);

    const docsArray = [];
    docsBdArray.forEach((doc) => {
      docsArray.push(doc.data());
    });

    return docsArray;
  } catch (e) {
    console.log(e);
    return false;
  }
};
